<template>
  <div class="container">
    <div class="wraper_name">
     <div class="back-btn">
      <backBtn @click="$router.go(-1)" />
    </div>    
    <form enctype="multipart/form-data" @submit.prevent="handleClick" >
      <inputText :warna="warna" idName="inputPost" textLabel="Caption" :textarea="true" placeholder="Apa yang anda pikirkan?" @data-input="handleCapt"/>
      <label for="postImg" class="label">
            <icon_image /> Pilih gambar
      </label>
      <input type="file" accept="image/jpg, image/png" id="postImg" style="display: none" @change="preview($event)" ref="gambar" multiple>
      <div v-if="image" class="gambar">
        <img :src="img" width="200" v-for="img in imgs" :key="img">
      </div>
      <sendBtn type="submit" v-wave="{ color: warna.primary, initialOpacity: 0.5, easing: 'ease-in' }" :warna="warna.primary" v-if="active"/>
     <sendBtn type="button" :warna="warna.text" v-else/>
   </form>
    </div>
    <p class="danger-text">{{ pesan }}</p>
  </div>
</template>

<script>
import inputText from '../components/etc/inputText.vue'
/*{
idName,
textLabel,
warna,
placeholder
}*/
import backBtn from '../components/mats/backBtn.vue'
import sendBtn from '../components/mats/sendBtn.vue'
import icon_image from '../icon/icon_image.vue'

  export default{
    name: 'changeName',
    data() {
      return {
        caption: '',
        image: false,
        active: false,
        pesan: '',
        example: null,
        imgs: [],
        imgArrays: null,
        token: localStorage.getItem('xtoken')
      }
    },
    components: {
      inputText,
      backBtn,
      sendBtn,
      icon_image
    },
    methods: {
      preview() {
        this.imgArrays = this.$refs.gambar.files;
        this.imgs = [];
        this.imgArrays.forEach((d) => {
          this.imgs.push(window.URL.createObjectURL(d));
        })
        this.image = (this.imgs[0]) ? true : false
      },
      handleCapt(data) {
        this.caption = data;
        this.validatornya();
      },
      validatornya() {
        if(this.caption.length >= 1){
          this.active = true;
          this.pesan = ''
        } else {
          this.active = false;
          this.pesan = 'Caption minimal 1 huruf'
        }
      },
      async handleClick() {
        try {
          console.log(this.imgArrays)
          const formData = new FormData();
          formData.append('text', this.caption)
          if(this.imgArrays) {
            this.imgArrays.forEach((img, i) => {
              formData.append(`image[${i}]`, img);
            })
          }
          const req = await this.axios.post('/Postdata', formData, {
            headers: {
              'Content-Type': "multipart/form-data",
              authorization: `Bearer ${this.token}`
            }
          })
          console.log(req.data)
        } catch(e) {
          alert(e);
        }
      }
    },
    props: {
      warna: {
        type: Object,
        required: true
      }
    },
    created() {
      this.$emit('hilang-navbar', false)
    }
  }
</script>

<style scoped>
.container {
  position: relative;
  width: 100%;
  padding: 0.3rem 0.5rem;
}
.wraper_name {
  max-width: 800px;
  margin: 0 auto;
}
.danger-text {
  color: #a32222;
  font-size: 0.6rem;
}
.label {
  display: flex;
  align-items: center;
  grid-gap: 0.4rem;
}
.gambar {
  padding: 0.3rem 0.5rem;
}
</style>
